import React, { Component } from 'react';
import { get, getRequest, getTourAuthKey, isNullOrEmpty } from 'utils';
import TourItem from './TourItem';
import { connect } from 'react-redux';
import * as action from 'action';
import { blankDiv } from 'utils';
import './Tour.css';
import $ from 'jquery';
import TourCategoryItem from './TourCategoryItem';

const uri = 'tour/areaBasedList';

class Tour extends Component {
    constructor(props){
    super(props);

    if (this.props.history != null) {
      this.props.setHistory(this.props.history);
      $('#dv-body-back.gradient-1').css('opacity', 0);
      $('#dv-body-back.gradient-2').css('opacity', 0);
      $('#dv-body-back.gradient-3').css('opacity', 1);
    }

    this.enumCat = {
      'A01': '자연',  // 산/계곡
      'A02': '인문(문화/예술/역사)',
      'A03': '레포츠',
      'C01': '추천코스',
      'B02': '숙박',
      'A04': '쇼핑',
      'A05': '음식',
    }

    this.eToCat = {
      'A01': '1', 
      'A02': '2', 
      'A03': '4', 
      'C01': '3',
      'B02': '0',
      'A04': '0',
      'A05': '0',
    }

    this.category = [
      { value: '0', label: '평창휴캉스', icon: 'home', filters: ['숙박', '음식', '쇼핑']},
      { value: '1', label: '산/계곡', icon: 'filter_hdr', filters: ['자연']},
      { value: '2', label: '관광지', icon: 'account_balance', filters: ['인문(문화/예술/역사)']},
      { value: '3', label: '둘레길', icon: 'nature_people', filters: ['추천코스']},
      { value: '4', label: '레저/스포츠', icon: 'golf_course', filters: ['레포츠']},
    ];

    this.state = {
      data: {},
      category: '0',
    }
  }

  componentDidMount() {
    if (!this.props.tourData) {
      this.requestList();
    }
  }

  onClickCategory(key) {
    $('.anchor').css('transform', 'translateX(-50%)');
    this.setState({
      ...this.state,
      category: key,
    })
  }

  requestList() {
    var data = {
      ServiceKey: getTourAuthKey(),
      //contentTypeId: 12,
      areaCode: 32,
      sigunguCode: 15,
      listYN: 'Y',
      MobileOS: window.os,
      MobileApp: window.appName,
      arrange: 'A',
      numOfRows: 9999,
      pageNo: 1,
      _type: 'json',
    }
    
    var sortingData = {

    };

    get(uri, data, 
      (res) => {
        var result = res.response.body.items.item;
        result.forEach(element => {
          var key = this.eToCat[element.cat1];

          if (!(key in sortingData))  {
            sortingData[key] = [];
          }
          //console.log(element);
          sortingData[key].push(element);
        });

        console.log(sortingData);

        this.setState({
          ...this.state,
          data: sortingData,
        })
        this.props.setTourData(sortingData);
      }, 
      (res) => {
        console.log(res);
      });
  }

  renderCategory() {
    var list = [];
    this.category.forEach(item => {
      list.push(<TourCategoryItem onClick={ (key) => { this.onClickCategory(key); } } 
        key={item.value} 
        data={item} />);
    })
    return(<div>
      {list}
    </div>);
  }

  renderList() {
    if (!this.props.tourData) {
      return(<div>
      </div>);
    }
    var data = this.props.tourData[this.state.category];
    console.log(data);
    if (data && data.length > 0) {
      console.log('in');
      var list = [];
      var count = 0;
      data.forEach(item => {
        if (!isNullOrEmpty(item.firstimage))
          item.firstimage = item.firstimage.replace('http://', 'https://');
        if (!isNullOrEmpty(item.firstimage2))
          item.firstimage2 = item.firstimage2.replace('http://', 'https://');

        if(!isNullOrEmpty(item.firstimage) && !isNullOrEmpty(item.addr1))
          list.push(<TourItem key={count++} data={item} />);
      })
      return(<div>
        {list}
      </div>);
    }
    return(<div>
    </div>);
  }

  render(){
    return (
      <div id='dv-tour'>
        { blankDiv('60px') }
        <div id='dv-tour-list'>
          <div className='anchor transition-default'>
            <div className='scroll-view contents'>
              <div className='container'>
                {this.renderCategory()}
              </div>
            </div>
            <div className='scroll-view items'>
              <div className='button-back material-icons'
                onClick={()=>{ $('.anchor').css('transform', 'translateX(0%)'); }}> arrow_back </div>
              <div className='container'>
                {this.renderList()}
              </div>
            </div>
          </div>
        </div>
      </div>);
  }
  /**
   * 
   * navigator.geolocation.getCurrentPosition((position)=>{alert('Lat: ' + position.coords.latitude + ' ' + 'Lon: ' + position.coords.latitude);});
  */
}


const mapStateToProps = (state) => {
  return {
    tourData: state.tourData
      //user: state.auth.user
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) },
    setTourData: (tourData) => { dispatch(action.setTourData(tourData)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Tour);