import React, { Component } from 'react';
import LinkButton from 'component/UI/LinkButton';
import { connect } from 'react-redux';
import * as action from 'action';
import { blankDiv } from 'utils';
import './Main.css';
import $ from 'jquery';

class Main extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
      this.props.setHistory(this.props.history);
      $('#dv-body-back.gradient-1').css('opacity', 1);
      $('#dv-body-back.gradient-2').css('opacity', 0);
      $('#dv-body-back.gradient-3').css('opacity', 0);
    }
  }

  render(){
    return (
      <div id='dv-main'>
          {blankDiv('50px')}
        <div id='dv-main-button-list' className={this.props.showMenu ? 'show': 'hide'}>
          <div id='dv-main-buttons'>
            <LinkButton idx={1} 
              icon={'map'}
              text={'평창 소개'} 
              link='/'/>
              
            <LinkButton idx={2} 
              text={'관광지 소개'} 
              icon={'tour'}
              link='/tour'/>
          </div>
          <div id='dv-main-buttons'>      
            <LinkButton idx={3} 
              icon={'location_on'}
              text={'AR 길찾기'} 
              link='/poi'/>
              
            <LinkButton idx={4} 
            text={'AR 체험'} 
              icon={'games'}
              link='/game'/>
          </div>
          <div style={{width: '80vw', marginLeft: '10vw', fontSize: '12px'}}>
            { blankDiv('570px') }
            <b>browser info: </b>
            <br/>
            { window.navigator.userAgent }
            <br/>
            <br/>          
            <b>os:</b>{` ${window.os}`}
            { blankDiv('70px') }
          </div>
        </div>
      </div>);
  }
}

const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
      showMenu: state.showMenu
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) },
    setShowMenu: (showMenu) => { dispatch(action.setShowMenu(showMenu)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);