import React, { Component } from 'react';
import * as action from 'action';
import { connect } from 'react-redux';
import './POI.css';

class POI extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
       this.props.setHistory(this.props.history);
    }

    this.state = {
    }
  }

  componentDidMount(){
    window.initTabs('#dv-poi ul.tabs', {
      swipeable : true,
      responsiveThreshold : '100vw'
    });
    //$('ul.tabs').tabs();
    //$('ul.tabs').tabs('select', 'test1');

  }

  onClick(id) {
    //window.selectTab(id);
  }

  render(){
    return (
      <div id='dv-poi'>
        <div className="row">
          <div className="col s12">
            <ul id='tabs' className="tabs">
              <li className="tab col s3"><a className='active' href="#test1">Test 1</a></li>
              <li className="tab col s3"><a href="#test2">Test 2</a></li>
              <li className="tab col s3"><a href="#test3">Test 3</a></li>
              <li className="tab col s3"><a href="#test4">Test 4</a></li>
              <li className="tab col s3"><a href="#test5">Test 5</a></li>
            </ul>
          </div>
          <div id="test1" className="col s12">Test 1</div>
          <div id="test2" className="col s12">Test 2</div>
          <div id="test3" className="col s12">Test 3</div>
          <div id="test4" className="col s12">Test 4</div>
          <div id="test5" className="col s12">Test 5</div>
        </div>
      </div>);
  }
}

const mapStateToProps = (state) => { return {}; }

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(POI);