import React, { Component } from 'react';
import './Logo.css';
import { connect } from 'react-redux';
import * as action from 'action';
import { blankDiv } from 'utils';

class Logo extends Component {
  constructor(props){
    super(props);
    
    this.pageInfo = [
      '/main',
      '/game',
      '/tour',
      '/poi'
    ]
    this.offsetX = window.innerWidth - 50;
    this.offsetY = 70;
    this.state = {
      index: 0,
    }

    this.oldCenter = 0;
    this.center = 0;
  }

  getIndex(val){
    return this.pageInfo.indexOf(val);
  }

  componentDidUpdate() {
    var idx = this.getIndex(this.props.history.location.pathname);
    if (idx > -1 && this.state.index != idx) {
      this.oldCenter = this.center;
      this.center = idx;
      this.setState({
        ...this.state,
        index: idx,
      });
    }
  }

  onClick() {
    if (!this.props.showMenu) {
    }
    this.props.setShowMenu(!this.props.showMenu);
  }

  getStyle(idx) {
    var gap = idx - this.state.index;
    gap = gap > 1 ? 1 : (gap < -1 ? -1 : gap);
    const left = gap * this.offsetX;
    const scale = 1 - Math.abs(gap) * 0.25;
    const top = 50 + Math.abs(gap) * this.offsetY;
    
    return { 
      left: left,
      top: top,
      transform: `scale(${scale})`,
    }
  }

  renderTitleLogo(idx) {
    var style = this.getStyle(idx);
    var transition = `transition-${(this.center == idx || this.oldCenter == idx) ? 'default' : 'none'}`;
    
    return (
      <div id={`logo-${idx}`} 
        style={style} 
        className={`logo ${this.props.showMenu ? 'small': 'large'} ${transition}`}
        onClick={()=>{ this.onClick(); }}>
        <img src='resources/logo.png'/>
      </div>
    );
  }

  renderLogo(idx, icon, title) {
    var style = this.getStyle(idx);
    var transition = `transition-${(this.center == idx || this.oldCenter == idx) ? 'default' : 'none'}`;

    return (
      <div id={`logo-${idx}`} style={style} className={`logo small ${transition}`}>
        <div id='dv-logo'>
          <span className='material-icons'>{icon}</span>
          <div>
            <span className='logo-title' >{title}</span>
          </div>
        </div>
      </div>
    );
  }

  render(){
    return (
      <div id='dv-logo-container'>
        <div id='dv-body-back' className='gradient-1'/>
        <div id='dv-body-back' className='gradient-2'/>
        <div id='dv-body-back' className='gradient-3'/>
        {this.renderTitleLogo(0)}
        {this.renderLogo(1, 'games', 'AR체험')}
        {this.renderLogo(2, 'tour', '관광지 소개')}
        {this.renderLogo(3, '', '빈칸')}
      </div>);
  }
}

const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
      showMenu: state.showMenu,
      history: state.history,
      state: state.history ? state.history.location.pathname : '',
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) },
    setShowMenu: (showMenu) => { dispatch(action.setShowMenu(showMenu)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Logo);