import React, { Component } from 'react';
import "./TourItem.css";
import "./TourCategoryItem.css";

class TourCategoryItem extends Component {
  constructor(props){
    super(props);

    this.state = {
    }
  }

  onClick() {
    if (this.props.data) {
      this.props.onClick(this.props.data.value);
    }
  }

  render(){
    return (
      <div>
        <div id='dv-tour-item' onClick={ () => { this.onClick(); } }>
          <div className='tour-item'>
            <div className={`category icon category-gradient-${this.props.data.value} material-icons`}>
              {this.props.data.icon}
            </div>
            <p className='tour-name'>
              {this.props.data.label}
            </p>
          </div>
          <div id='dv-tour-arrow'> 
            <div className='tour-arrow' />
          </div>
        </div>  
      </div>);
  }
}

export default TourCategoryItem;